import React from 'react'
import './about.css'
import ME from '../../assets/me-about.jpg'
import {FaAward} from 'react-icons/fa'
// import {FiUsers} from 'react-icons/fi'
import {FaLaptopCode} from 'react-icons/fa'
// import {VscFolderLibrary} from 'react-icons/vsc'
import {AiFillCode} from 'react-icons/ai'

const About = () => {
  return (
    <section id='about'>
      <h5>Get To Know</h5>
      <h2>About Me</h2>

      <div className="container about__container">
        <div className="about__me">
          <div className="about__me-image">
            <img src={ME} alt="About Image" />
          </div>
        </div>

        <div className="about__content">
          <div className="about__cards">
            <article className='about__card'>
              <FaAward className='about__icon'/>
              <h5>Experience</h5>
              <small>4+ Years Working</small>
            </article>

            <article className='about__card'>
              {/* <FiUsers className='about__icon'/> */}
              <AiFillCode className='about__icon'/>
              <h5>Code</h5>
              <small>5+ Languages</small>
            </article>

            <article className='about__card'>
              {/* <VscFolderLibrary className='about__icon'/> */}
              <FaLaptopCode className='about__icon'/>
              <h5>Skills</h5>
              <small>5+ Frameworks</small>
            </article>
          </div>

          <p>
            Recent graduate with a B.S. in Mathematics and a concentration in Probability and Statistics. Experience with programming in Python, R, SQL, Node.js, and machine learning packages such as tensorflow, scikit-learn, and keras.
          </p>

          <a href="#contact" className='btn btn-primary'>Let's Talk</a>
        </div>
      </div>
    </section>
  )
}

export default About